import { css, InterpolationFunction } from 'styled-components';

export interface BreakWordProps {
    breakWord?: boolean;
}

/*
 *     
 */
export const applyHyphens: InterpolationFunction<BreakWordProps> = ({ breakWord = true }) =>
    breakWord &&
    css`
        overflow-wrap: break-word;
        hyphens: auto;
    `;
