import React, { FC, createContext } from 'react';

export const SSRContext = createContext<{ uniqId: number | null }>({
    uniqId: null,
});

/**
 *    ,      .
 *
 *      Radiobox  Checkbox .
 */
export const SSRProvider: FC = ({ children }) => {
    const value = {
        uniqId: 0,
    };

    return <SSRContext.Provider value={value}>{children}</SSRContext.Provider>;
};
